@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap');

body{
  font-family: 'Poppins', sans-serif!important;
  height:100%;
}
.App {
  text-align: center;
  
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.loader{
  width: 100vw;
    height: 100vh;
    background-color:#1b1e2d;

}

#root{
  height: var(--vh);
}
.main{
  position: absolute;
  display: flex;
  height:100%;
  width: 100vw;
}

.sidebar{
  padding:1rem;
  display: flex;
  flex-direction: column;
}
.userlistimg{
  width:3rem;
  border-radius:50%;
}
.plusaddf{
  text-align: center;
    padding: 0.8rem;
    font-size: 1.5rem;
}
.users-list{
  display: flex;
  flex-direction: column;
  gap: 0.6rem;
}
.userlistimg:hover{
  transform:scale(1.04);
  cursor:pointer;
}
.navbar{
  padding: 1rem;
  display: flex;
  align-items:center;
    justify-content: right;
}

.inner-main{
  display: flex;
  flex-direction: column;
  width: 100%;

}
.inner-main-content{
  height:100%;
  width:100%;
}
.inner-main-content>div{
  height: 100%;
  display:flex;
  justify-content: end;
  flex-direction: column;
}
.sendmessagebox>textarea{
padding:0.4rem;
border-radius:50px;
}
.chatbox{
  max-height: calc(100vh - 200px);
  overflow: scroll;
  display:flex;
  justify-content: end;
  padding:1rem 0.5rem;
  flex-direction: column;
  gap:0.6rem;
}
.sendmessagebox{
  display: flex;
  justify-content: space-between;
  padding: 1rem;
}
#expandingTextarea {
  resize: none;
  overflow: hidden;
  height: 34px;
  max-height: 200px;
  padding: 0.3rem 1rem;
  width: 80%;/* You can adjust the max height as per your requirement */
}
.message{
  display:flex;
}
.pfpimg{
  width:3rem;
  height:3rem;
  padding: 0.3rem;
    border-radius: 50%;
}
.authorimg{
  width:3.5rem;
  height:3.5rem;
  padding: 0.3rem;
    border-radius: 50%;
}
.message{
  gap:0.7rem;
}
.message-top{
  width: 100%;
  display: flex;
  justify-content: space-between;

}
.message-top>p:nth-child(1){
  font-weight: 800;
    font-size: 1rem;
    margin-top: 0.5rem;
}
.message-top>p:nth-child(2){
  font-weight: 700;
    opacity: 0.7;
    font-size: 0.7rem;
    display:none;
}
.message:hover>div>.message-top>p:nth-child(2){
  display:flex;
}
.message>div{
  width:100%;
}
.pfpimg:hover{
  transform:scale(1.05);
  cursor:pointer;
}
.changestatusbtn{
  display: flex;
  align-items: center;
  gap:1rem;
  justify-content: space-between;
}
.changestatusbtn>span{
  background-color:var(--chakra-colors-green-500);
  height:1rem;
  width:1rem;
  border-radius: 50%;
}